	data() {
		return {
			iconList: [{
				icon: 'cardboardfill',
				color: 'red',
				badge: 120,
				name: 'VR'
			}, {
				icon: 'recordfill',
				color: 'orange',
				badge: 1,
				name: '录像'
			}, {
				icon: 'picfill',
				color: 'yellow',
				badge: 0,
				name: '图像'
			}, {
				icon: 'noticefill',
				color: 'olive',
				badge: 22,
				name: '通知'
			}, {
				icon: 'upstagefill',
				color: 'cyan',
				badge: 0,
				name: '排行榜'
			}, {
				icon: 'clothesfill',
				color: 'blue',
				badge: 0,
				name: '皮肤'
			}, {
				icon: 'discoverfill',
				color: 'purple',
				badge: 0,
				name: '发现'
			}, {
				icon: 'questionfill',
				color: 'mauve',
				badge: 0,
				name: '帮助'
			}, {
				icon: 'commandfill',
				color: 'purple',
				badge: 0,
				name: '问答'
			}, {
				icon: 'brandfill',
				color: 'mauve',
				badge: 0,
				name: '版权'
			}],
			gridCol: 3,
			skin: false,
			modalName: '',
			gridBorder: '',
			menuBorder: '',
			menuArrow: '',
			menuCard: '',
			ListTouchStart: '',
			ListTouchDirection: '',

		};
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target;
		},
		hideModal(e) {
			this.modalName = null;
		},
		gridchange: function(e) {
			this.gridCol = e.detail.value;
		},
		gridswitch: function(e) {
			this.gridBorder = e.detail.value;
		},
		menuBorders: function(e) {
			this.menuBorder = e.detail.value;
		},
		menuArrows: function(e) {
			this.menuArrow = e.detail.value
		},
		menuCards: function(e) {
			this.menuCard = e.detail.value;
		},
		switchSex: function(e) {
			this.skin = e.detail.value;
		},

		// ListTouch触摸开始
		ListTouchStarts(e) {
			console.log('aaaa');
			this.ListTouchStart = e.touches[0].pageX;
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.ListTouchDirection = e.touches[0].pageX - this.ListTouchStart > 0 ? 'right' : 'left';
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.ListTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target;
			} else {
				this.modalName = null;
			}
			this.ListTouchDirection = null;
		}
	}
}
